.appointment-field {
  width: 50%;
  padding: 56px 168px 66px 100px;
  color: $color-default-white;
  background-color: $Sapphire;

  @media (max-width: $tablet-width) {
    width: 100%;
    padding: 61px 163px 67px 133px;
  }

  @media (max-width: $mobile-width) {
    padding: 64px 15px 67px;
  }
}

.appointment-field__form {
  display: flex;
  flex-direction: column;
  width: 417px;
  margin-left: auto;

  @media (max-width: $tablet-width) {
    width: 100%;
    margin-left: 0;
  }
}

.appointment-field__form-title {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-weight: 800;
  line-height: 84.24px;
  letter-spacing: 1.62px;
  margin: 0 0 17px 3px;

  @media (max-width: $tablet-width) {
    font-size: 42px;
    letter-spacing: 1.1px;
    line-height: normal;
    margin: 0 0 34px 0;
  }

  @media (max-width: $mobile-width) {
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}

.appointment-field__form-group {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  border: none;
}

.appointment-field__form-group span {
  position: absolute;
  content: "";
  top: 28px;
  width: 300px;
}

.appointment-field__form-group-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-bottom: 26px;

  @media (max-width: $tablet-width) {
    gap: 60px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 5px;
    grid-template-columns: none;
    grid-template-rows: 1fr 1fr;
    gap: 0;
  }
}

.appointment-field__form-label {
  display: block;

  @media (max-width: $tablet-width) {
    margin-right: 0;
  }

  @media (max-width: $mobile-width) {
    &:first-of-type {
      margin-bottom: 33px;
    }
  }
}

.appointment-field__form input {
  display: block;
  font-size: 14px;
  width: 100%;
  padding: 3px 0 6px 0;
  color: $color-default-white;
  background-color: $Sapphire;
  border: none;
  border-bottom: 1px solid $light-slate-grey;
  box-sizing: border-box;

  &:hover {
    border-bottom: 1px solid $color-default-white;
  }

  &:focus,
  &:active {
    outline: 1px solid $color-default-white;
    border: none;
  }

  &:disabled {
    opacity: 0.4;
  }
}

.appointment-field__form input::placeholder {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  color: $color-default-white;
}

.appointment-field__form input:focus::placeholder,
.appointment-field__form input:active::placeholder {
  color: $color-transparent;
}

.appointment-field__form input:disabled::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.appointment-field__form button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  width: 100%;
  padding: 18px;
  color: $color-default-white;
  background-color: $Sapphire;
  border: 3px solid $color-default-white;
  cursor: pointer;

  &:focus {
    outline: 3px solid $color-default-white;
    background-color: $color-default-white;
    color: #103aae;
  }

  &:hover {
    outline: 3px solid $color-default-white;
    background-color: $color-default-white;
    color: #103aae;
  }

  &:active {
    background-color: $color-default-white;
    color: $Sapphire;
  }
}

[data-message-base="   "],
[data-message-extra="   "] {
  color: #ff121f;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
}
